.add-modal {
  z-index: 1;
  width: 100px;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  color: #FFF;
  font-size: 1.325rem;
  font-weight: 600;
  padding: 0.5em 2em;
  margin-bottom: 1em;
  border-radius: 20px;
  background-color: #242424;
}